html {
  font-family: Arial sans-serif;
  font-size: 16px;
}

body {
  margin: 0;
  padding: 0;
}

.people {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  gap: 30px;
  margin: 0 auto;
  padding: 30px 35px;
  transition: transform 1s;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

.person__photo {
  position: relative;
}

.person__photo img {
  border-radius: 50%;
  width: 100%;
  margin-bottom: 8px;
  transition: border-radius 1s 1s;
  display: block;
}

.person__rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  line-height: 32px;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.person__details {
  flex-direction: column;
  justify-content: center;
  gap: 10px;
  display: flex;
}

.person__name {
  align-self: center;
  font-size: 20px;
  line-height: 23px;
}

.person__extra {
  align-self: center;
  display: none;
}

.person:hover {
  background: #eee;
  border-radius: 12px;
  padding: 12px;
  transform: scale(1.2);
}

.person:hover .person__photo img {
  border-radius: 60px;
}

.person:hover .person__extra {
  z-index: 2;
  display: block;
  position: absolute;
  top: 100%;
}
